.header .header-wrap .header-nav > ul > li .nav-tags {
          background-color: #fff;
          position: absolute;
          z-index: 1000;
          width: 100%;
          font-size: 14px;
          color: #555555;
          -webkit-transition: all 0.5s;
          transition: all 0.5s;
          opacity: 0;
          -webkit-transform: scaleY(0);
          transform: scaleY(0);
          -webkit-transform-origin: top;
          transform-origin: top;
