          display: inline-block;
          font-size: 16px;
          color: #333333;
          -webkit-transition: 0.5s;
          transition: 0.5s;
          position: relative;
}
.header .header-wrap .header-nav > ul > li > a::after {
          content: '';
          -webkit-transition: all 0.5s;
          transition: all 0.5s;
          display: block;
          border-bottom: 1px solid #604775;
          position: absolute;
          bottom: 8px;
          left: 0;
          width: 0%;
}
.header .header-wrap .header-nav > ul > li > a::before {
